<template>
    <div v-resize="resize">
        <canvas class="canvas-size" ref="gcodeViewer"></canvas>
        <div class="main-container">
			<v-row no-gutters>
				<v-col cols="12">
            <v-btn><v-icon @click="chooseFile">mdi-upload</v-icon></v-btn>
            <v-btn><v-icon @click="reload">mdi-play</v-icon></v-btn>
			</v-col>
			<v-col cols="12">
			<v-text-field v-model="voxelWidth" class="input-width"></v-text-field>
			</v-col>
			<v-col cols="12">
			<v-text-field v-model="voxelHeight" class="input-width"></v-text-field>
			</v-col>
			</v-row>
            <input ref="fileInput" type="file" :accept="'.g,.gcode,.gc,.gco,.nc,.ngc,.tap'" hidden @change="fileSelected" multiple />
        </div>
        <div class="loading-progress">
            <v-progress-linear rounded v-show="loading" height="15" :value="loadingProgress" class="disable-transition">
                {{loadingProgress}}%
            </v-progress-linear>
        </div>
    </div>
</template>



<style scoped>
.canvas-size {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	right: 0;
	bottom: 0;
	background-color: yellow;
}

.main-container {
	position: absolute;
	right: 5px;
	top: 5px;
	width:150px;
}

.loading-progress {
	position: absolute;
	width: 50%;
	left: 0px;
	margin-left: 25%;
	top: 5px;
	z-index: 19 !important;
}

.input-width{ 
	width : 100px;
}
</style>

<script>
'use strict';

import GCodeViewer from './viewer/gcodeviewer.js';
import { RenderMode } from './viewer/gcodeprocessor.js';

let viewer = null;

export default {
	data() {
		return {
			loading: false,
			loadingProgress: 0,
			voxelWidth: 1,
			voxelHeight: 1,
		};
	},
	computed: {},
	mounted() {
		viewer = new GCodeViewer(this.$refs.gcodeViewer);
		viewer.init();

		viewer.gcodeProcessor.loadingProgressCallback = progress => {
			this.loadingProgress = Math.ceil(progress * 100);
		};

		viewer.gcodeProcessor.renderMode = RenderMode.voxel;
		viewer.gcodeProcessor.meshBreakPoint = Number.MAX_VALUE; //We are going to overwrite the breakpoint for voxels (for now)  Voxel Engine will run it's own SPS. SPS per layer height?
	},
	methods: {
		resize() {
			if (viewer !== null) {
				viewer.resize();
			}
		},
		async fileSelected(e) {

			viewer.gcodeProcessor.voxelWidth = isNaN(this.voxelWidth) ? 5 :  this.voxelWidth;
			viewer.gcodeProcessor.voxelHeight = isNaN(this.voxelHeight) ? 5 : this.voxelHeight;

			const reader = new FileReader();
			reader.addEventListener('load', async event => {
				const blob = event.target.result;
				await viewer.processFile(blob);
				//this.setGCodeValues();
				this.loading = false;
			});
			this.loading = true;
			reader.readAsText(e.target.files[0]);
			e.target.value = '';
		},
		chooseFile() {
			this.$refs.fileInput.click();
		},
		reload() {
			viewer.gcodeProcessor.voxelWidth = isNaN(this.voxelWidth) ? 5 :  this.voxelWidth;
			viewer.gcodeProcessor.voxelHeight = isNaN(this.voxelHeight) ? 5 : this.voxelHeight;
			viewer.reload();
		}
	},
};
</script>



